
.session-container {
	max-width: unset;
	width: unset;
}
.trailer {
	text-decoration: none;
}
@media (min-width: 720px) {
	.session-container td.conference-td {
		padding: 0 0 15px 50px;
	}
	.conference-title-wrapper.label:before,
	.conference-title-wrapper.label:after {
		left: -50px;
	}
}
.conference-title-wrapper.label {
	padding-left: 0;
	padding-right: 0;
	margin-left: 0;
}
@media (min-width: 720px) {
	.q-qsc-section h3 {
		margin-bottom: 1.526em;
	}
}
.q-qsc-video__icon {
	position: relative;
	display: block;
}
.q-qsc-video__icon:hover .overlay {
	width: 100%;
	height: 100%;
	position:absolute;
	background-color:#000;
	opacity:0.5;
}
.video-container {
	width: 322px !important;
	margin-bottom: 40px;
}
@media (min-width: 425px) {
	.video-container {
		margin-bottom: 20px;
	}
}
.q-qsc-videos__container .video {
	width: 329px;
	height: 185px;
	display: block;
	background-color: #D8D8D8;
	margin-bottom: 1.5em;
	position: relative;
}
.q-qsc-videos__container .poster {
	margin-bottom: 1.5em;
	border: 1px solid #ccc;
}
.q-qsc-videos__container .video .play-icon {
	position: absolute;
	left: auto;
	right: 0;
	top: 0;
	width: 48px;
	height: 48px;
}
.q-qsc-videos__container .video .lock-icon {
	height: 29px;
	margin-top: 15px;
	margin-left: 2px;
}
.q-qsc-videos__container .video .play-icon .bg {
	transition: fill 0.25s;
}
.q-qsc-videos__container .video:hover .play-icon .bg {
	fill: #E3241C;
}
.q-qsc-videos__container .videos {
	justify-content: flex-start;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 2.1em;
}
.q-qsc-videos__container .videos .video-container .blue-div{
	display: flex;
	align-items: center;
	font-family: Gotham, sans-serif;
	padding: 10px 0;
}
.q-qsc-videos__container .videos .video-container .blue-div .top-text-icon{
	width: 35px;
	height: 35px;
}
.q-qsc-videos__container .videos .video-container .blue-div .top-text-icon .triangle_play{
	transform: translate(-42px, -43px) scale(2.3);
} 
.q-qsc-videos__container .videos .video-container .blue-div .top-text{
	text-transform: uppercase;
	color: #6495ED;
	line-height: 1.25;
	font-weight: 500;
	padding-left: 10px;
	letter-spacing: 1px;
}
@media (min-width: 1408px) {
	.q-qsc-videos__container .videos {
		flex-wrap: wrap;
	}
}
.video-container .title,
.video-container .subtitle {
	font-size: 17px;
	font-weight: 500;
	font-family: Gotham, sans-serif;
	color: #262626;
	line-height: 1.4;
}
.video-container .subtitle {
	color: #6A778B;
	margin-top: 0;
	margin-bottom: 0;
	font-weight: 400;
}
@media (min-width: 425px) {
	.video-container .title {
		font-size: 20px;
	}
	.video-container .subtitle {
		font-size: 18px;
	}
}
.video-container .speaker {
	font-size: 17px;
	font-family: Gotham, sans-serif;
	color: #262626;
	line-height: 1.4;
	margin-top: 5px;
	margin-bottom: 4px;
}
@media (min-width: 425px) {
	.video-container .speaker {
		font-size: 18px;
	}
}
.video-type-container {
	margin-bottom: 5.3em;
}
.session-types {
	margin-top: 1em;
	margin-bottom: 2em;
}
.heading--2 {
	margin-bottom: 1.2em;
}
.note {
	color: #6a778b;
	font-family: Gotham, sans-serif;
	font-style: italic;
	font-size: 13px;
	margin-top: 0;
	line-height: 1.4;
}
.panelists-link:hover {
	cursor: pointer;
}
.speaker.panelists {
	font-size: 15px;
}
.interviews .video-container {
	margin-bottom: 36px;
}
.panelists b {
	font-weight: 500;
}
.speaker-line-break {
	display: block;
	margin-bottom: 5px;
}
.video-container:nth-child(2n),
.video-container:nth-child(3n) {
	margin-right: 2em;
}